<template>
  <div class="body">
    <div class="container system">
      <v-sidebar :data="navs"></v-sidebar>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        navs: [
          {
            title: '组织',
            children: [
              {title: '组织类型', path: '/sys/cate'},
              {title: '组织管理', path: '/sys/om'}
            ]
          },
          {
            title: '用户',
            children: [
              {title: '用户组管理', path: '/sys/group'},
              {title: '用户管理', path: '/sys/user'}
            ]
          },
          {
            title: '权限',
            children: [
              {title: '用户组权限', path: '/sys/permGroup'},
              {title: '用户权限', path: '/sys/permUser'}
            ]
          },
          {
            title: '服务',
            children: [
              {title: '服务管理', path: '/sys/service'}
            ]
          },
          {
            title: '页面',
            children: [
              {title: '页面管理', path: '/sys/page'}
            ]
          },
          {
            title: '日志',
            children: [
              {title: '日志管理', path: '/sys/log'}
            ]
          }
        ]
      }
    }
  }
</script>


