<template>
  <div class="body">
    <div class="container system">
      <v-sidebar :data="navs" logo-key="resource" logo-text="资源管理" :icon-index="7"></v-sidebar>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        navs: [
          {
            title: '布控',
            children: [
              {title: '布控配置',path: '/resource/controlCog'}
            ]
          },
          {
            title: '设备',
            children: [
              {title: '设备管理',path: '/resource/device'},
              {title: '设备布控',path: '/resource/control'}
            ]
          },
          {
            title: '人像库',
            children: [
              {title: '人像库管理',path: '/resource/faceset'}
            ]
          }
        ]
      }
    }
  }
</script>


